// Большие девайсы (большие десктопы, >= 1200px)
@media (min-width: 1200px) {  }

// Большие девайсы (большие десктопы, >= 992px)
@media (max-width: 1199.98px) {

  .art-navbar {
    .navbar-collapse {
      .navbar-nav {
        li {
          padding: 5px 9px;

          a {
            font-size: 12px;
          }
        }
      }
    }
  }

}

// Средние девайсы («таблетки», >= 768px)
@media (max-width: 991.98px) {

  .home-content {
    padding: 50px 15px;
  }

  .home-nav {
    li {
      padding: 2px 0;

      a {
        padding: 20px;
        width: 100%;
        display: block;
      }
    }
  }

  .art-navbar {
    .navbar-collapse {
      text-align: center;

      .navbar-nav {
        padding: 20px 0;

        li {
          a {
            font-size: 16px;
          }
        }

        &__lang {
          padding: 0 0 10px 0;
          padding-left: 0;
          flex-direction: row;
          justify-content: center;

          li {
            a {
              font-size: 18px;
            }
          }
        }
      }
    }
  }

}

// Малые девайсы («ландшафтные», >= 575px)
@media (max-width: 767.98px) {  }

// Экстрамалые девайсы («портретные телефоны», < 576px)
@media (max-width: 575.98px) {

  .footer {
    flex-direction: column;
    text-align: center;

    &__copyright {
      width: 100%;
    }

    &__social {
      width: 100%;
      margin: 20px 0;

      &--item {
        margin: 0 auto;
      }
    }

    &--totop {
      width: 100%;
      text-align: center;
    }
  }

}
